.mobile-menu .menu-list > * {
  padding-left: 25px;
  padding-right: 25px;
  position: relative;
  -moz-box-shadow: inset 0px 1px 0px 0px rgba(51, 51, 51, 0.1) !important;
  -webkit-box-shadow: inset 0px 1px 0px 0px rgba(51, 51, 51, 0.1) !important;
  box-shadow: inset 0px 1px 0px 0px rgba(51, 51, 51, 0.1) !important; }
  .mobile-menu .menu-list > *:last-child {
    box-shadow: inset 0px 1px 0px 0px rgba(51, 51, 51, 0.1), inset 0px -1px 0px 0px rgba(51, 51, 51, 0.1) !important; }
  .mobile-menu .menu-list > * a {
    padding-top: 27px;
    padding-bottom: 27px; }
  .mobile-menu .menu-list > *.tab-link a:after {
    content: '';
    height: 10px;
    width: 10px;
    border-top: 1px solid rgba(51, 51, 51, 0.5);
    border-right: 1px solid rgba(51, 51, 51, 0.5);
    position: absolute;
    right: 15px;
    margin-bottom: 5px;
    top: 50%;
    -webkit-transform: translateY(-50%) rotate(45deg) translateZ(0);
    -moz-transform: translateY(-50%) rotate(45deg) translateZ(0);
    -o-transform: translateY(-50%) rotate(45deg) translateZ(0);
    transform: translateY(-50%) rotate(45deg) translateZ(0); }
