.mobile-menu {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100vw;
  left: 125vw;
  z-index: 200;
  background: white;
  overflow: hidden;
  white-space: nowrap;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: transform 0.25s ease-in-out;
  -moz-transition: transform 0.25s ease-in-out;
  -o-transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out; }
  .mobile-menu__tabs-wrapper {
    padding-top: 70px;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll; }

#mobile-menu__wrapper.open .mobile-menu {
  -webkit-transform: translateX(-125vw);
  -moz-transform: translateX(-125vw);
  -o-transform: translateX(-125vw);
  transform: translateX(-125vw); }
  @media (min-width: 992px) {
    #mobile-menu__wrapper.open .mobile-menu {
      -webkit-transform: translateX(0);
      -moz-transform: translateX(0);
      -o-transform: translateX(0);
      transform: translateX(0); } }

#mobile-menu__wrapper.open .mobile-menu__overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 200;
  background: rgba(51, 51, 51, 0.75); }
