.mobile-menu__heading {
  top: 0;
  left: 0;
  position: absolute;
  background: white;
  height: 70px;
  white-space: nowrap;
  z-index: 1; }
  .mobile-menu__heading .logo {
    width: 120px !important;
    height: 40px !important; }

.mobile-menu .logo__wrapper {
  position: relative;
  display: inline-block;
  padding-left: 15px;
  padding-top: 15px;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  width: 100vw;
  vertical-align: top;
  opacity: 1;
  -webkit-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  -moz-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  -o-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out; }

.mobile-menu .tab-link-back {
  height: 12px;
  vertical-align: top;
  display: inline-block;
  position: absolute;
  top: 32.5px;
  text-align: center;
  -webkit-transform: translateX(-100vw) translateZ(0);
  -moz-transform: translateX(-100vw) translateZ(0);
  -o-transform: translateX(-100vw) translateZ(0);
  transform: translateX(-100vw) translateZ(0);
  width: 100vw;
  opacity: 0;
  padding-left: 0px !important;
  -webkit-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  -moz-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  -o-transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out; }
  .mobile-menu .tab-link-back:before {
    position: absolute !important;
    top: 0;
    left: 15px !important;
    width: 10px !important;
    height: 10px !important;
    border-left: 1px solid rgba(51, 51, 51, 0.5) !important;
    border-bottom: 1px solid rgba(51, 51, 51, 0.5) !important; }

.mobile-menu.secondary-tab-active .logo__wrapper {
  -webkit-transform: translateX(-100vw) translateZ(0);
  -moz-transform: translateX(-100vw) translateZ(0);
  -o-transform: translateX(-100vw) translateZ(0);
  transform: translateX(-100vw) translateZ(0);
  opacity: 0; }

.mobile-menu.secondary-tab-active .tab-link-back {
  -webkit-transform: translateX(0) translateZ(0);
  -moz-transform: translateX(0) translateZ(0);
  -o-transform: translateX(0) translateZ(0);
  transform: translateX(0) translateZ(0);
  opacity: 1; }
